import React, { useState, useEffect, useRef } from 'react';
import '../styles/projects.css';
import '../styles/nav.css';
import allProjects from '../allProjects.json';
import ProjectDetails from './projectDetails';
import Nav from './nav';

const Projects = () => {
	const [id, changeId] = useState(0);
	const [moreOpened, openMore] = useState(false);
	const projectContainer: React.MutableRefObject<any> = useRef();

	interface dataType {
		[key: number]: {
			id: number;
			textColor: string;
			bgColor: string;
			image: string;
			title: string;
			summary: string;
			description: any;
		};
	}

	const data: dataType = allProjects;
	const currentProject = data[id];

	const style = {
		background: `${currentProject.bgColor}`,
		color: `${currentProject.textColor}`,
	};

	const btnStyle = {
		border: `1px solid ${currentProject.textColor}`,
		color: `${currentProject.textColor}`,
	};

	useEffect(() => {
		projectContainer.current.classList.add('show');
	}, []);

	return (
		<div className='projects'>
			<div className='projects-content' style={style} ref={projectContainer}>
				<div className='projects-description'>
					<h3>{currentProject.title}</h3>
					<p>{currentProject.summary}</p>
					<button
						style={btnStyle}
						onClick={() => {
							openMore(true);
							document.querySelector('body')?.classList.add('no-scroll');
						}}>
						More Info.
					</button>
				</div>
				<div
					className='projects-image'
					onClick={() => openMore(true)}
					style={{ backgroundImage: `url(${currentProject.image})` }}
				/>
			</div>

			<Nav currentId={id} changeId={changeId} />

			{moreOpened ? (
				<ProjectDetails
					close={openMore}
					project={currentProject}
					nav={<Nav currentId={id} changeId={changeId} />}
				/>
			) : (
				''
			)}
		</div>
	);
};

export default Projects;
